import { useState } from "react/cjs/react.development";
import { useApp } from "../AppProvider";
import { H1, H3 } from "../components/Headers";

export default function Contact () {
  const {router: {page}, auth: {isAuthenticated}, contact: {messages, getMessages, sendMessage}, useForm} = useApp ();
  
  const [messageErr, setMessageErr] = useState (null);
  const {set, get, submit} = useForm (values => {
    if (!values.name) return setMessageErr ('Please enter your name!');
    if (!values.to) return setMessageErr ('Please select a someone to message!');
    if (!values.message) return setMessageErr ('Please enter a message to send us!');
    if (!values.contact) return setMessageErr ('Please enter your contact information!');
    sendMessage (values);
  });
    
  if (page !== '/Clergymen/contact') return '';
  if (isAuthenticated) return (
    <>
      <div className="text-center">
        <H1>Messages:</H1>
      </div>
      <main>
        {
          messages.map (message => (
            <div>{JSON.stringify (message)}</div>
          ))
        }
      </main>
    </>
  );
  return (
    <>
      <div className="text-center">
        <H1>Contact The Clergymen:</H1>
      </div>
      <main>
        {
          messageErr &&
          <div className="error">{messageErr}</div>
        }
        <div className="form-group">
          <H3>Your Name:</H3>
          <input name="name" value={get ('name')} onChange={set ('name')} />
        </div>
        <div className="form-group">
          <H3>To:</H3>
          <input name="to" value={get ('to')} onChange={set ('to')} />
        </div>
        <div className="form-group">
          <H3>Message:</H3>
          <textarea name="message" value={get ('message')} onChange={e => set ('message') ({
            target: {
              value: e.target.innerText
            }
          })} />
        </div>
        <div className="form-group">
          <H3>Your Contact:</H3>
          <input name="contact" value={get ('contact')} onChange={set ('contact')} />
        </div>
        <div className="form-group">
          <button onClick={submit}>Send Message</button>
        </div>
      </main>
    </>
  )
}